export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'പ്രധാന പ്രശ്നം';
            case 'edit_pratilipi_summary':
            	return 'സംഗ്രഹം (നിര്‍ബന്ധമില്ല) :';
            case 'edit_pratilipi_title':
            	return 'തലവാചകം';
            case 'pratilipi_delete_content':
                return 'നീക്കം ചെയ്യൂ';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['പ്രധാന പ്രശ്നം'];
            case 'edit_pratilipi_summary':
            	return ['സംഗ്രഹം (നിര്‍ബന്ധമില്ല)'];
            case 'edit_pratilipi_title':
            	return ['തലവാചകം ഇവിടെ എഴുതൂ', 'തലവാചകം ഇംഗ്ലീഷില്‍ എഴുതൂ'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'ഈ രചന നീക്കം ചെയ്യട്ടേ?';
        }
    }
}



