<template>
    <MainLayout>
        <div class="settings-page">
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <div class="head-title">Settings</div>
                        <button type="button" class="sign-out btn btn-light" name="button" @click="logoutUser"><i class="material-icons">power_settings_new</i> സൈന്‍ ഔട്ട്‌</button>
                        <div class="settings-menu">
                            <a href="#" v-on:click="tabchange" class="active" data-tab="profile-settings">Profile</a>
                            <a href="#" v-on:click="tabchange" data-tab="notification-settings">നോട്ടിഫിക്കേഷന്‍സ്</a>
                            <a href="#" v-on:click="tabchange" data-tab="password-settings">പാസ്സ്‌വേര്‍ഡ്‌ മാറ്റൂ</a>
                        </div>
                        <div class="menu-contents">
                            <div class="tabs" id="profile-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-first-name">പേര് (മലയാളത്തില്‍) *</label>
                                            <!-- <input type="text" class="form-control" id="pratilipi-settings-first-name" :value="firstName" @input="updateFirstName" placeholder="പേര് (മലയാളത്തില്‍)"> -->
                                            <TranslatingInput :value="firstName" :oninput="updateFirstName"></TranslatingInput>
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-last-name"></label>
                                            <!-- <input type="text" class="form-control" id="pratilipi-settings-last-name" :value="lastName" @input="updateLastName" placeholder=""> -->
                                            <TranslatingInput :value="lastName" :oninput="updateLastName"></TranslatingInput>
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-first-name-en">പേര് (ഇംഗ്ലീഷില്‍)) *</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-first-name-en" :value="firstNameEn" @input="updateFirstNameEn" placeholder="പേര് (ഇംഗ്ലീഷില്‍))">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-last-name-en"></label>
                                            <input type="text" class="form-control" id="pratilipi-settings-last-name-en" :value="lastNameEn" @input="updateLastNameEn" placeholder="">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-pen-name">തൂലികാനാമം (മലയാളത്തില്‍) *</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-pen-name" :value="penName" @input="updatePenName" placeholder="തൂലികാനാമം (മലയാളത്തില്‍)">
                                        </div>
                                        <div class="form-group">
                                            <label for="pratilipi-settings-language">ഭാഷ തെരഞ്ഞെടുക്കൂ *</label>
                                            <select class="form-control" id="pratilipi-settings-language" @input="updateLanguage">
                                                <option disabled selected>ഭാഷ തെരഞ്ഞെടുക്കൂ</option>
                                                <option :selected="eachLanguage.fullName.toUpperCase() === language" :value="eachLanguage.fullName.toUpperCase()" v-for="eachLanguage in constants.LANGUAGES" :key="eachLanguage.shortName">{{ eachLanguage.fullName.toUpperCase() }}</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-summary">സംഗ്രഹം (നിര്‍ബന്ധമില്ല)</label>
                                            <textarea class="form-control" id="pratilipi-settings-summary" :value="summary" @input="updateSummary" rows="3"></textarea>
                                        </div>
                                    </div>
                                    <div class="section-title">Private Information</div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-email">ഇമെയില്‍ അഡ്രസ്സ്</label>
                                            <input type="email" class="form-control" id="pratilipi-settings-email" :value="email" @input="updateEmail" placeholder="ഇമെയില്‍ അഡ്രസ്സ്">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-phone">ഫോണ്‍ നമ്പര്‍</label>
                                            <input type="tel" class="form-control" id="pratilipi-settings-phone" :value="phone" @input="updatePhone" placeholder="ഫോണ്‍ നമ്പര്‍">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-gender">ലിംഗം</label>
                                            <select class="form-control" id="pratilipi-settings-gender" @input="updateGender">
                                                <option disabled selected>ലിംഗം</option>
                                                <option :selected="'MALE' === gender" value="MALE">പുരുഷന്‍</option>
                                                <option :selected="'FEMALE' === gender" value="FEMALE">സ്ത്രീ</option>
                                                <option :selected="'OTHER' === gender" value="OTHER">മറ്റുള്ളവ</option>
                                            </select>
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-dateOfBirth">ജനനത്തിയ്യതി</label>
                                            <input type="date" class="form-control" id="pratilipi-settings-dateOfBirth" :value="dateOfBirth" @input="updateDateOfBirth" placeholder="ജനനത്തിയ്യതി">
                                        </div>
                                    </div>
                                    <button type="button" class="btn btn-primary update-btn" @click="updateProfileSettings">മാറ്റങ്ങള്‍ സേവ് ചെയ്യുക</button>
                                </form>
                            </div>
                            <div class="tabs" id="notification-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-email-frequency">ഇമെയില്‍ അയക്കേണ്ട രീതി</label>
                                            <select class="form-control" id="pratilipi-settings-email-frequency">
                                                <option disabled selected>ഇമെയില്‍ അയക്കേണ്ട രീതി</option>
                                                <option>ഉടനെ</option>
                                                <option>ദിവസത്തില്‍ ഒരിക്കല്‍</option>
                                                <option>ആഴ്ചയില്‍ ഒരിക്കല്‍</option>
                                                <option>മാസത്തില്‍ ഒരിക്കല്‍</option>
                                                <option>ഒരിക്കലും വേണ്ട</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="section-title">രചന</div>
                                    <div class="form-row">
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_review">
                                            <label class="form-check-label" for="notif_option_new_review">പുതിയ റിവ്യൂ</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_comment">
                                            <label class="form-check-label" for="notif_option_new_comment">പുതിയ കമന്റ്</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_like_review">
                                            <label class="form-check-label" for="notif_option_like_review">റിവ്യൂയ്ക്ക് ലൈക്ക്</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_like_comment">
                                            <label class="form-check-label" for="notif_option_like_comment">കമന്റിന് പുതിയ ലൈക്ക്</label>
                                        </div>
                                    </div>
                                    
                                    <div class="section-title">നെറ്റ് വര്‍ക്ക്</div>
                                    <div class="form-row">
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_follower">
                                            <label class="form-check-label" for="notif_option_new_follower">പുതിയ ഫോളോവര്‍</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_pratilipi_published_follower">
                                            <label class="form-check-label" for="notif_option_pratilipi_published_follower">നിങ്ങള്‍ ഫോളോ ചെയ്യുന്നവരില്‍ നിന്നും പുതിയ രചന</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_pratilipi_updates">
                                            <label class="form-check-label" for="notif_option_pratilipi_updates">പ്രതിലിപിയുടെ അപ്ഡേറ്റുകളും ഓഫറുകളും</label>
                                        </div>
                                    </div>
                                    <button type="submit" class="btn btn-primary update-btn" disabled>മാറ്റങ്ങള്‍ സേവ് ചെയ്യുക</button>
                                </form>
                            </div>
                            <div class="tabs" id="password-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-current-password">നിലവിലുള്ള പാസ്സ്‌വേര്‍ഡ്‌ *</label>
                                            <input type="password" class="form-control" v-model="oldPassword" id="pratilipi-settings-current-password" placeholder="നിലവിലുള്ള പാസ്സ്‌വേര്‍ഡ്‌">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-new-password">പുതിയ പാസ്സ്‌വേര്‍ഡ്‌ *</label>
                                            <input type="password" class="form-control" id="pratilipi-settings-new-password" placeholder="പുതിയ പാസ്സ്‌വേര്‍ഡ്‌">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-confirm-password">പാസ്സ്‌വേര്‍ഡ്‌ ഒന്നു കൂടി ടൈപ്പ് ചെയ്യൂ *</label>
                                            <input type="password" class="form-control" v-model="newPassword" id="pratilipi-settings-confirm-password" placeholder="പാസ്സ്‌വേര്‍ഡ്‌ ഒന്നു കൂടി ടൈപ്പ് ചെയ്യൂ">
                                        </div>
                                    </div>
                                    <button type="button" class="btn btn-primary update-btn" @click="updateUserPassword({ oldPassword, newPassword })">മാറ്റങ്ങള്‍ സേവ് ചെയ്യുക</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </MainLayout>
</template>

<script>
import MainLayout from '@/layout/main-layout.vue';
import TranslatingInput from '@/components/TranslatingInput.vue';
import constants from '@/constants';
import { mapGetters, mapActions, mapState } from 'vuex'

export default {
    components: {
        MainLayout,
        TranslatingInput
    },
    data() {
        return {
            constants,
            oldPassword: '',
            newPassword: ''
        }
    },
    computed: {
        ...mapGetters('settingspage', [
            'getAuthorData'
        ]),
        ...mapState({
            email: state => state.user.data.email,
            phone: state => state.user.data.phone,
            firstName: state => state.settingspage.author.data.firstName,
            language: state => state.settingspage.author.data.language,
            lastName: state => state.settingspage.author.data.lastName,
            firstNameEn: state => state.settingspage.author.data.firstNameEn,
            lastNameEn: state => state.settingspage.author.data.lastNameEn,
            penName: state => state.settingspage.author.data.penName,
            summary: state => state.settingspage.author.data.summary,
            gender: state => state.settingspage.author.data.gender,
            dateOfBirth: state => state.settingspage.author.data.dateOfBirth
        }),
        ...mapGetters([
            'getUserDetails',
            'getLogoutStatus'
        ]),
    },
    methods: {
        ...mapActions('settingspage', [
            'fetchAuthorDetails',
            'updateUserDetails',
            'updateAuthorDetails',
            'updateUserPassword'
        ]),
        ...mapActions([
            'logoutUser'
        ]),
        tabchange(event) {
            event.preventDefault();        
            var tab_id = $(event.currentTarget).attr('data-tab');
            $(".settings-menu a").removeClass("active");
            $(event.currentTarget).addClass("active");
            $(".tabs").hide();
            $("#" + tab_id).show();
        },
        updateEmail(e) { this.$store.commit('settingspage/updateEmail', e.target.value) },
        updatePhone(e) { this.$store.commit('settingspage/updatePhone', e.target.value) },
        updateFirstName(value) { this.$store.commit('settingspage/updateFirstName', value) },
        updateLanguage(e) { this.$store.commit('settingspage/updateLanguage', e.target.selectedOptions[0].value) },
        updateLastName(value) { this.$store.commit('settingspage/updateLastName', value) },
        updateFirstNameEn(e) { this.$store.commit('settingspage/updateFirstNameEn', e.target.value) },
        updateLastNameEn(e) { this.$store.commit('settingspage/updateLastNameEn', e.target.value) },
        updatePenName(e) { this.$store.commit('settingspage/updatePenName', e.target.value) },
        updateSummary(e) { this.$store.commit('settingspage/updateSummary', e.target.value) },
        updateGender(e) { this.$store.commit('settingspage/updateGender', e.target.selectedOptions[0].value) },
        updateDateOfBirth(e) { this.$store.commit('settingspage/updateDateOfBirth', e.target.value) },
        updateProfileSettings() {
            this.updateUserDetails();
            this.updateAuthorDetails();
        }
    },
    watch: {
        'getUserDetails.authorId'(newValue) {
            if (newValue) {
                this.fetchAuthorDetails(newValue);
            }
        },
        'getUserDetails.isGuest'(isGuest) {
            if (isGuest) {
                this.$router.push('login');
            }
        },
        'getLogoutStatus'(loggedOut) {
            if (loggedOut) {
                location.reload()    
            }
        }
    },
    created() {
        if (this.getUserDetails.isGuest) {
            this.$router.push('login');
        }

        if (this.getUserDetails.authorId) {
            this.fetchAuthorDetails(this.getUserDetails.authorId);    
        }
    }
}
</script>

<style lang="scss" scoped>
.settings-page {
    margin-top: 85px;
    text-align: left;
    @media screen and (max-width: 992px ) {
        margin-top: 65px;
    }
    .head-title {
        font-size: 18px;
        font-weight: bold;
        text-align: left;
        border-left: 3px solid #d0021b;
        padding-left: 10px;
        margin: 10px 0 0;
        float: left;
    }
    .sign-out {
        display: inline-block;
        color: #d0021b;
        float: right;
        margin-top: 10px;
        font-size: 14px;
        i {
            vertical-align: middle;
            font-size: 18px;
        }
    }
    .settings-menu {
        border-bottom: 1px solid #e9e9e9;
        padding: 8px 0 10px;
        text-align: left;
        overflow: hidden;
        width: 100%;
        overflow-x: auto;
        white-space: nowrap;
        clear: both;
        a {
            color: #555;
            font-size: 13px;
            border-bottom: 2px solid #fff;
            padding: 5px 5px 11px;
            span {
                font-size: 12px;
                font-weight: bold;
                padding: 0 2px;
                margin-right: 5px;
                display: inline-block;
                text-align: center;
            }
            &.active {
                color: #d0021b;
                border-color: #d0021b;
                span {
                    color: #d0021b;
                }
            }
            &:hover {
                text-decoration: none;
                color: #d0021b;
            }
        }
    }
    .tabs {
        margin: 20px 0;
        display: none;
        &#profile-settings {
            display: block;
        }
        label {
            font-size: 12px;
        }
        .section-title {
            font-size: 16px;
            margin: 10px 0;
            font-weight: bold;
        }
        .form-check {
            width: 100%;
            margin: 0 10px 5px 15px;
            label {
                font-size: 14px;
                vertical-align: text-bottom;
            }
        }
        .update-btn {
            background: #d0021b;
            border: 0;
            outline: none;
            font-size: 14px;
            float: right;
            margin: 10px 0;
        }
    }
}
</style>



